
.header {
    padding: 20px;
    text-align: center;
    position: sticky;
    top: 0;
    z-index: 1000;
    padding-top: 2.5%;
    padding-right: 5%;

}
  
.navList {
    list-style: none; 
    display: flex;
    justify-content: right;
    gap: 50px;
    padding: 0;  
    margin: 0;
}
  
  
.navList li a { 

    color: white;
    text-decoration: none;
    font-size: 18px;
    transition: color 0.3s ease;
  
    &:hover {
      color: #888786; 
      
    }
  
}
  